import React from 'react'
import RedeemAddress from '../models/redeem_address'

type Props = {
  model: RedeemAddress
  onSubmit: (model: RedeemAddress) => any
}
type State = {
  model: RedeemAddress
}

class RedeemForm extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props)
    this.state = {
      model: props.model,
    }
    this.onChange = this.onChange.bind(this)
    this.onSubmit = this.onSubmit.bind(this)
  }

  render() {
    const { model } = this.props
    
    return (
      <form onSubmit={this.onSubmit}>
        <input
          type="text" name="country" placeholder="country" value={model.Country}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            model.Country = event.currentTarget.value
            this.onChange(model)
          }}
        />
        <br/>
        <input
          type="text" name="city" placeholder="city" value={model.City}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            model.City = event.currentTarget.value
            this.onChange(model)
          }}
        />
        <br/>
        <input
          type="text" name="address" placeholder="address" value={model.Address}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
            model.Address = event.currentTarget.value
            this.onChange(model)
          }}
        />
        <br/>
        <button type="submit">Submit</button>
      </form>)
  }

  private onChange(model: RedeemAddress) {
    const clone = Object.assign({}, model) as RedeemAddress
    this.setState({
      model: clone
    })
  }

  private onSubmit(event: React.SyntheticEvent<HTMLFormElement>) {
    event.preventDefault()
    this.props.onSubmit(this.state.model)
  }
}

export default RedeemForm